import {Component, ViewChild} from '@angular/core';
import {FormBuilder, FormControl, FormGroup, MaxLengthValidator, NgForm, Validators} from '@angular/forms';
import {AuthService} from '../auth.service';
import { HttpClient } from '@angular/common/http';
import { Subject } from 'rxjs';
import { ActivatedRoute, Router } from "@angular/router"
import { BaseServiceService } from '@app/services/base-service.service';
import axios from 'axios';

@Component({
  selector: 'app-signin',
  templateUrl: './signin.component.html',
  styleUrls: ['./signin.component.scss']
})
export class SigninComponent {
  loginForm: FormGroup;
  submitted = false;
   msg:String="";
   loginStatus:boolean = false;
   id:any = null;
  email = new FormControl('');
  password = new FormControl('');
  keepMeLoggedIn = new FormControl(true);

  // @ViewChild('signinForm') form: NgForm;

  constructor(private authService: AuthService,
    private baseService:BaseServiceService,
    private _route: ActivatedRoute,
    private formBuilder: FormBuilder,
    private router: Router) {
       if(window.localStorage.getItem('loginType') == 'Mobile')
          this.loginStatus = true;
       if(!!window.localStorage.getItem('happiTicketToken'))
       {
         var self =this;
        setTimeout(()=>{
          self.router.navigateByUrl('/ticket/ticket-list');
        },500)
       }
      this.id = this._route.snapshot.queryParamMap.get('id');
      if(this.id !=null && this.id != undefined)
      {
         window.localStorage.setItem("loginType","Mobile")
        this.loginStatus = true;
        axios.get(this.baseService.baseUrl+"userlog/getTokenDetails/" +this.id, {
     })    
       .then(response => {
         console.log(response);
         if(response.data.status)
         {
            window.localStorage.setItem('happiTicketToken', response.data.data.token);
           delete response.data.data.token,
           delete response.data.notificattionToken,
            window.localStorage.setItem('userData', JSON.stringify(response.data.data));
           this.baseService.update();
           this.router.navigate(['/ticket/ticket-list']);
           this.submitted=false;
          this.loginForm.reset();
         } 
         else
         {
           this.msg=response.data.message;
         }
       })
       .catch(function (error) {
         console.log(error)
       })
      }
      else
      {
         window.localStorage.setItem("loginType","Web")
        this.router.navigate(['']);
      }
      // if(!! window.localStorage.getItem('happiTicketToken'))
      //  this.router.navigate(['/ticket/ticket-list']);
      // else
      // this.router.navigate([''])
     
     }


    ngOnInit() {
      this.loginForm = this.formBuilder.group({
        phone: ['', [Validators.required, Validators.pattern("^((\\+91-?)|0)?[0-9]{10}$")]],
        password: ['', Validators.required],
        keepMeLoggedIn: [true, Validators.requiredTrue]
      });
  }

  // convenience getter for easy access to form fields
  get f() { return this.loginForm.controls; }

 
  onSubmitSignIn() {
    this.submitted = true;

    // stop here if form is invalid
    if (this.loginForm.invalid) {
        return;
    }
    // this.authService.signinUser(this.email.value, this.password.value);
        axios.post(this.baseService.baseUrl + 'userlog/user_loggin', {
           phone:this.loginForm.get("phone").value,
           password:this.loginForm.get("password").value
      })    
        .then(response => {
          console.log(response);
          if(response.data.status)
          {
            this.msg='';
             window.localStorage.setItem('happiTicketToken', response.data.token);
             window.localStorage.setItem('userData', JSON.stringify(response.data.data));
            this.baseService.update();
           this.router.navigate(['/ticket/ticket-list']);
           this.submitted=false;
           this.loginForm.reset();
          } 
          else
          {
            this.msg=response.data.message;
          }
        })
        .catch(function (error) {
          console.log(error)
        })

  }

  onReset() {
    this.submitted = false;
    this.loginForm.reset();
}

}
